<!-- Êî∂ËøêÊ∏ÖÂçï -->
<template>
    <view class="container">
        <uni-nav-bar dark :fixed="true" background-color="#fff" status-bar left-icon="left" color="#000" title="Êî∂ËøêÊ∏ÖÂçï"
            @clickLeft="back" />
        <view class="menu">
            <view class="tab-bar">
                <view v-for="(tab, index) in tabs" :key="index" class="tab-item"
                    :class="{ active: currentTab === index }" @click="handleTabClick(index)">
                    {{ tab.value }}
                    <!-- ‰∏∫È¢ÑÁ∫¶‰∏≠tabÊ∑ªÂä†uni-badge -->
                    <!-- <uni-badge v-if="tab.value === 'È¢ÑÁ∫¶‰∏≠' && bookingBadgeText !== '0' && bookingBadgeText !== ''"
                        class="uni-badge" type="error" :text="bookingBadgeText" :is-dot="false" absolute="rightTop"
                        :offset="[-5, -12]"></uni-badge> -->
                    <!-- ‰∏∫ËøõË°å‰∏≠tabÊ∑ªÂä†uni-badge -->
                    <uni-badge v-if="tab.value === 'ËøõË°å‰∏≠' && processingBadgeText !== 0" class="uni-badge" type="error"
                        :text="processingBadgeText" :is-dot="false" absolute="rightTop" :offset="[-5, -12]"></uni-badge>
                    <view class="tab-line" v-if="currentTab === index"></view>
                </view>
            </view>
        </view>

        <!-- ÂÜÖÂÆπÂå∫Âüü -->
        <view class="content-wrapper">
            <scroll-view class="content" scroll-y>
                <!-- Êï∞ÊçÆÂàóË°® -->
                <view class="order-list" v-if="allOrderList.length > 0">
                    <view class="order-item" v-for="(item, index) in allOrderList" :key="index">
                        <view class="order-header">
                            <view class="shop-info">
                                <text class="shop-name">{{ item.merchantName }}</text>
                                <text class="status-tag" :class="getStatusClass(item.status)">
                                    {{ getStatusText(item.status) }}
                                </text>
                            </view>

                        </view>
                        <view class="order-content">
                            <view class="info-item">
                                <text class="label">È¢Ñ‰º∞Êî∂ËøêÔºö</text>
                                <text class="value">{{ item.estimateWeight }} Ê°∂</text>
                            </view>
                            <view class="info-item">
                                <text class="label">‰ªäÊó•Êî∂ËøêÔºö</text>
                                <text class="value">{{ item.estimateBucketNum ?? 0 }} Ê°∂</text>
                            </view>
                            <view class="info-item">
                                <text class="label">È¢Ñ‰º∞ÊÄªÈáèÔºö</text>
                                <text class="value">{{ item.estimateWeight ?? 0 }} kg</text>
                            </view>
                            <view class="info-item">
                                <text class="label">Êî∂ËøêÊÄªÈáèÔºö</text>
                                <text class="value">{{ item.weight ?? 'ÊöÇÊó†' }}kg</text>
                            </view>
                            <view class="info-item">
                                <text class="label">ËΩ¶ËæÜ‰ø°ÊÅØÔºö</text>
                                <text class="value">{{ item.registrationNumber ?? 'ÊöÇÊó†' }}</text>
                            </view>
                            <view class="info-item">
                                <text class="label">Êî∂ËøêÊó∂Èó¥Ôºö</text>
                                <text class="value">{{ item.arrivalTime ?? 'ÊöÇÊó†' }}</text>
                            </view>
                        </view>
                        <view class="order-footer">
                            <!-- currentTab == 0: ÊòæÁ§∫ÂèñÊ∂àÂíåÊü•ÁúãËØ¶ÊÉÖÊåâÈíÆ -->
                            <template v-if="currentTab == 0">
                                <uni-button v-if="item.status == 0" size="mini" type="default" class="btn-cancel"
                                    @tap="handleCancel(item)">
                                    ÂèñÊ∂à
                                </uni-button>
                                <uni-button size="mini" type="primary" class="btn-confirm"
                                    @tap="handleViewDetails(item)">
                                    Êü•ÁúãËØ¶ÊÉÖ
                                </uni-button>
                            </template>

                            <!-- currentTab == 1: ÊòæÁ§∫Á°ÆËÆ§Êî∂ËøêÊåâÈíÆ -->
                            <template v-else-if="currentTab == 1">
                                <uni-button size="mini" type="primary" class="btn-confirm"
                                    @tap="handleConfirmTransport(item)">
                                    Á°ÆËÆ§Êî∂Ëøê
                                </uni-button>
                            </template>

                            <!-- currentTab == 2: Âè™ÊòæÁ§∫Êü•ÁúãËØ¶ÊÉÖÊåâÈíÆ -->
                            <template v-else-if="currentTab == 2">
                                <uni-button size="mini" type="default" class="btn-confirm"
                                    @tap="handleViewDetails(item)">
                                    Êü•ÁúãËØ¶ÊÉÖ
                                </uni-button>
                            </template>
                        </view>
                    </view>

                    <!-- Âä†ËΩΩÊõ¥Â§öÁªÑ‰ª∂ - Âè™Âú®ÊúâÊï∞ÊçÆÊó∂ÊòæÁ§∫ -->
                    <view class="loadMore">
                        <uni-load-more :status="loadingStatus" :content-text="{
                            contentdown: '‰∏äÊãâÊòæÁ§∫Êõ¥Â§ö',
                            contentrefresh: 'Ê≠£Âú®Âä†ËΩΩ...',
                            contentnomore: 'Ê≤°ÊúâÊõ¥Â§öÊï∞ÊçÆ‰∫Ü'
                        }"></uni-load-more>
                    </view>
                </view>

                <!-- ÊöÇÊó†Êï∞ÊçÆÁä∂ÊÄÅ -->
                <view class="empty-state" v-else-if="loadingStatus !== 'loading'">
                    <view class="empty-icon">üìã</view>
                    <text class="empty-text">ÊöÇÊó†Êï∞ÊçÆ</text>
                    <text class="empty-desc">ÂΩìÂâçÂàÜÁ±ª‰∏ãÊöÇÊó∂Ê≤°ÊúâÁõ∏ÂÖ≥‰ø°ÊÅØ</text>
                </view>
            </scroll-view>
        </view>
    </view>
</template>
<script setup>
import {
    ref,
    computed,
    onMounted
} from 'vue';
import {
    onPullDownRefresh,
    onReachBottom
} from '@dcloudio/uni-app';

import {
    apiGetPlanPage, apiGetconfirmPlanById, apiGetcancelPlanById, apiGetMerchantNotConfirmNum
} from '@/api/apis.js';

import { useUserStore } from '@/stores/user.js'

//3 È¢ÑÁ∫¶‰∏≠ 0 ÂæÖÊî∂Ëøê 1 Â∑≤ÂÆåÊàê
const tabs = [{ key: "3", value: "È¢ÑÁ∫¶‰∏≠" }, { key: "0", value: "ËøõË°å‰∏≠" }, { key: "1", value: "Â∑≤ÂÆåÊàê" }];
//‰∏ãÊ†á
const currentTab = ref(0);

const userStore = useUserStore();

//ËøîÂõû‰∏ä‰∏ÄÈ°µ
const back = () => {
    uni.navigateBack()
}

//ÁÇπÂáªËèúÂçï
function handleTabClick(index) {
    currentTab.value = index;
    allOrderList.value = [];
    pageNum.value = 1; // ÈáçÁΩÆÈ°µÁ†Å‰∏∫1
    getNetwork();
}

// Áä∂ÊÄÅËΩ¨Êç¢ÂáΩÊï∞

const getStatusText = (status) => {
    if (currentTab.value == 0) {
        // ÂÆ°Ê†∏Áä∂ÊÄÅ
        switch (status) {
            case 0: return 'ÂæÖÂÆ°Ê†∏';
            case 1: return 'ÂÆ°Ê†∏ÈÄöËøá';
            case 2: return 'Êú™ÈÄöËøá';
            default: return '';
        }
    }
    else {
        switch (status) {
            case 0: return 'ËøõË°å‰∏≠';
            case 1: return 'Â∑≤ÂÆåÊàê';
            case 2: return 'Êó†Ê≥ïÊî∂Ëøê';
            default: return '';
        }

    }
  
};

// Ëé∑ÂèñÁä∂ÊÄÅÊ†∑ÂºèÁ±ªÂêç
const getStatusClass = (status) => {
    if (currentTab.value == 0) {
        switch (status) {
            case 0: return 'booking'; // ÂæÖÂÆ°Ê†∏ 
            case 1: return 'passed'; // ÂÆ°Ê†∏ÈÄöËøá 
            case 2: return 'notpassed'; // ÂÆ°Ê†∏‰∏çÈÄöËøá 
            default: return '';
        }
    }
    else { 
        switch (status) {
            case 0: return 'processing';
            case 1: return 'completed';
            case 2: return 'cancelled';
            default: return '';
        }
    } 
};

// ÊåâÈíÆÁÇπÂáª‰∫ã‰ª∂Â§ÑÁêÜÂáΩÊï∞
const handleCancel = (item) => {
    console.log('ÂèñÊ∂àÊåâÈíÆË¢´ÁÇπÂáª111', item);
    uni.showModal({
        title: 'ÊèêÁ§∫',
        content: 'Á°ÆÂÆöË¶ÅÂèñÊ∂àÊ≠§È¢ÑÁ∫¶ÂêóÔºü',
        success: async (res) => {
            console.log('Áî®Êà∑ÁÇπÂáª‰∫ÜÁ°ÆÂÆöÊåâÈíÆ', res);
            if (res.confirm) {


                const resdata = await apiGetcancelPlanById({
                    merchantId: userStore.merchant?.id || 448,
                    id: item.id
                });

                if (resdata.code === 200) {
                    uni.showToast({
                        title: 'ÂèñÊ∂àÊàêÂäü',
                        icon: 'success',
                        duration: 2000
                    });
                    getNetwork();
                    getMerchantNotConfirmNum();
                }
                else {
                    uni.showToast({
                        title: 'ÂèñÊ∂àÂ§±Ë¥•',
                        icon: 'error',
                        duration: 2000
                    });
                }


            } else if (res.cancel) {
                // ÂèñÊ∂àÂèñÊ∂àËÆ¢Âçï
                console.log('ÂèñÊ∂àÂèñÊ∂àÈ¢ÑÁ∫¶');
            }
        },
    });
};

const handleViewDetails = (item) => {
    console.log('Êü•ÁúãËØ¶ÊÉÖÊåâÈíÆË¢´ÁÇπÂáª', item);

    if (currentTab.value == 0) {
        uni.navigateTo({
            url: `/pages/merchant/shyyDetail?id=${item.id}&merchantId =${item.merchantId}`
        });
    }
    else { 
        uni.navigateTo({
            url: `/pages/merchant/shsyDetail?id=${item.id}&merchantId =${item.merchantId}`
        });
    }

};

const handleConfirmTransport = async (item) => {
    console.log('Á°ÆËÆ§Êî∂ËøêÊåâÈíÆË¢´ÁÇπÂáª', item);
    if (item.arrivalTime == null) {
        uni.showToast({
            title: 'ËØ∑Á≠âÂæÖÂ∏àÂÇÖÁ°ÆËÆ§Êî∂ËøêÂÆåÊàê!',
            icon: 'none',
            dduration: 2500
        });
        return;
    }
    try {
        uni.showLoading({
            title: 'Á°ÆËÆ§‰∏≠...'
        });

        const params = {
            merchantId: userStore.merchant?.id,
            id: item.id
        };

        const res = await apiGetconfirmPlanById(params);

        uni.hideLoading();

        if (res.code === 200 || res.success) {
            uni.showToast({
                title: 'Á°ÆËÆ§Êî∂ËøêÊàêÂäü',
                icon: 'success'
            });

            // Âà∑Êñ∞ÂΩìÂâçÈ°µÈù¢Êï∞ÊçÆÔºå‰øùÊåÅÂú®ÂΩìÂâçÊ†áÁ≠æÈ°µ
            allOrderList.value = [];
            pageNum.value = 1;
            getNetwork();
        } else {
            uni.showToast({
                title: res.message || 'Á°ÆËÆ§Êî∂ËøêÂ§±Ë¥•',
                icon: 'none'
            });
        }
    } catch (error) {
        console.error('Á°ÆËÆ§Êî∂ËøêÂ§±Ë¥•:', error);
        uni.hideLoading();
        uni.showToast({
            title: 'ÁΩëÁªúÈîôËØØÔºåËØ∑ÈáçËØï',
            icon: 'none'
        });
    }
};

// Ê∑ªÂä†badgeTextÁöÑrefÂèòÈáè
const processingBadgeText = ref(0);

const getMerchantNotConfirmNum = async () => {
    const res = await apiGetMerchantNotConfirmNum({
        merchantId: userStore.merchant?.id || 448
    });
    if (res.code === 200) {
        processingBadgeText.value = res.data ?? 0;
    }
};



// Ê∑ªÂä†È°µÁ†ÅÂíåÂä†ËΩΩÁä∂ÊÄÅÂèòÈáè
const pageNum = ref(1);
const loadingStatus = ref('more'); // more-Âä†ËΩΩÂâç/loading-Âä†ËΩΩ‰∏≠/nomore-Ê≤°ÊúâÊõ¥Â§öÊï∞ÊçÆ

// Êï∞ÊçÆÂàóË°®
const allOrderList = ref([]);

//ÂêéËé∑ÂèñÊï∞ÊçÆ
const getNetwork = async () => {
    try {
        // Â¶ÇÊûú‰∏çÊòØÁ¨¨‰∏ÄÈ°µÔºåËÆæÁΩÆÂä†ËΩΩÁä∂ÊÄÅ‰∏∫Âä†ËΩΩ‰∏≠
        if (pageNum.value > 1) {
            loadingStatus.value = 'loading';
        }


        const res = await apiGetPlanPage({
            pageNum: pageNum.value,
            merchantId: userStore.merchant?.id ?? 448,
            status: tabs[currentTab.value].key // ‰ΩøÁî®tabs‰∏≠ÁöÑkeyÂÄº
        });


        // Â§ÑÁêÜ‰∏ãÊãâÂà∑Êñ∞
        if (pageNum.value === 1) {
            allOrderList.value = res.data.list || [];
            uni.stopPullDownRefresh();
        } else {
            // Â§ÑÁêÜ‰∏äÊãâÂä†ËΩΩÊõ¥Â§ö
            allOrderList.value = [...allOrderList.value, ...(res.data.list || [])];
        }

        // Âà§Êñ≠ÊòØÂê¶ËøòÊúâÊõ¥Â§öÊï∞ÊçÆ
        if (res.data.list && res.data.list.length < 10) {
            // Â¶ÇÊûúËøîÂõûÁöÑÊï∞ÊçÆÂ∞ë‰∫éÊØèÈ°µÊï∞ÈáèÔºåËØ¥ÊòéÊ≤°ÊúâÊõ¥Â§öÊï∞ÊçÆ‰∫Ü
            loadingStatus.value = 'nomore';
        } else {
            // Âê¶ÂàôËøòÊúâÊõ¥Â§öÊï∞ÊçÆ
            loadingStatus.value = 'more';
        }
    } catch (error) {
        console.error('Ëé∑ÂèñÊï∞ÊçÆÂ§±Ë¥•:', error);

        // ÂÅúÊ≠¢‰∏ãÊãâÂà∑Êñ∞
        uni.stopPullDownRefresh();

        // ÈáçÁΩÆÂä†ËΩΩÁä∂ÊÄÅ
        loadingStatus.value = 'more';

        // Â¶ÇÊûúÊòØÁ¨¨‰∏ÄÈ°µÂä†ËΩΩÂ§±Ë¥•ÔºåÁ°Æ‰øùÊòæÁ§∫ÊöÇÊó†Êï∞ÊçÆÁä∂ÊÄÅ
        if (pageNum.value === 1) {
            allOrderList.value = [];
        }

        uni.showToast({
            title: 'Êï∞ÊçÆÂä†ËΩΩÂ§±Ë¥•ÔºåËØ∑ÈáçËØï',
            icon: 'none',
            duration: 2000
        });
    }
};


// ‰∏äÊãâÂä†ËΩΩÊõ¥Â§öÊñπÊ≥ï
const onLoadMore = () => {
    if (loadingStatus.value === 'nomore') return;
    // È°µÁ†ÅÂ¢ûÂä†
    pageNum.value++;
    getNetwork();
};


// Ëß¶Â∫ïÂä†ËΩΩÊõ¥Â§ö
onReachBottom(() => {
    onLoadMore();
});

//‰∏ãÊãâÂà∑Êñ∞
onPullDownRefresh(() => {
    allOrderList.value = [];
    pageNum.value = 1; // ÈáçÁΩÆÈ°µÁ†Å‰∏∫1
    getNetwork();
    getMerchantNotConfirmNum();
})

// ÁªÑ‰ª∂ÊåÇËΩΩÊó∂ÂàùÂßãÂåñÊï∞ÊçÆ
onMounted(() => {
    pageNum.value = 1;
    getNetwork();
    getMerchantNotConfirmNum();
});
</script>

<style lang="scss" scoped>
.container {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    background-color: $bg-theme-color;

    .menu {
        position: relative;
        margin-top: 2rpx;
        background-color: #ffffff;
        height: 88rpx;

        .tab-bar {
            display: flex;
            height: 100%;

            .tab-item {
                flex: 1;
                height: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
                font-size: 28rpx;
                color: rgba(61, 61, 61, 1);
                position: relative;

                &.active {
                    color: rgba(7, 193, 96, 1);
                    font-weight: 500;
                }

                .tab-line {
                    position: absolute;
                    bottom: 0;
                    left: 50%;
                    transform: translateX(-50%);
                    width: 56rpx;
                    height: 4rpx;
                    background-color: rgba(7, 193, 96, 1);
                    border-radius: 2rpx;
                }
            }
        }
    }

    .content-wrapper {
        flex: 1;
        margin-top: 30rpx;

        .content {
            height: 100%;

        }

        .order-list {
            padding: 0 30rpx; // Â∑¶Âè≥30rpx

            .order-item {
                margin-bottom: 20rpx;
                padding: 30rpx;
                background-color: #ffffff;
                border-radius: 12rpx;

                .order-header {
                    margin-bottom: 20rpx;

                    .shop-info {
                        display: flex;
                        align-items: center;
                        justify-content: space-between;
                        margin-bottom: 16rpx;

                        .shop-name {
                            font-size: 28rpx;
                            font-weight: 400;
                            color: rgba(61, 61, 61, 1);
                        }

                        .status-tag {
                            border-radius: 8rpx;
                            font-size: 24rpx;
                            width: 120rpx;
                            height: 40rpx;
                            display: flex;
                            justify-content: center;
                            align-items: center;

                            &.booking {
                                color: rgba(255, 161, 0, 1);
                                background: rgba(255, 161, 0, 0.10);
                            }

                            &.passed {
                                color: rgba(7, 193, 96, 1);
                                background: rgba(7, 193, 96, 0.10);
                            }

                            &.notpassed {
                                color: rgba(221, 57, 47, 1);
                                background: rgba(221, 57, 47, 0.10);
                            }

                            &.processing {
                                //ËøõË°å‰∏≠ ÂæÖÂÆåÊàê
                                color: rgba(0, 170, 255, 1);
                                background: rgba(0, 170, 255, 0.10);
                            }

                            &.completed {
                                //Â∑≤ÂÆåÊàê
                                color: rgba(61, 61, 61, 0.50);
                                background: rgba(153, 153, 153, 0.1);
                            }

                            &.cancelled {
                                //Êó†Ê≥ïÊî∂Ëøê
                                color: rgba(255, 161, 0, 1);
                                background: rgba(255, 161, 0, 0.10);

                            }
                        }
                    }


                }

                .order-content {
                    padding: 20rpx 0;
                    border-top: 1px solid #f0f0f0;
                    border-bottom: 1px solid #f0f0f0;

                    .info-item {
                        display: flex;
                        margin-bottom: 16rpx;

                        &:last-child {
                            margin-bottom: 0;
                        }

                        .label {
                            font-size: 26rpx;
                            color: rgba(61, 61, 61, 0.50);
                        }

                        .value {
                            font-size: 26rpx;
                            color: rgba(61, 61, 61, 1);
                        }
                    }
                }

                .order-footer {
                    display: flex;
                    justify-content: flex-end;
                    margin-top: 20rpx;

                    .btn-cancel {
                        margin-right: 20rpx;
                        color: rgba(61, 61, 61, 1);
                        background-color: #fff;
                        border: 1px solid rgba(196, 196, 196, 1);
                        font-size: 26rpx;
                        width: 144rpx;
                        height: 48rpx;
                        border-radius: 20rpx;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        box-sizing: border-box; // ‰ΩøÁî®border-boxÁõíÊ®°Âûã

                    }

                    .btn-confirm {
                        color: rgba(255, 255, 255, 1);
                        background-color: rgba(7, 193, 96, 1);
                        font-size: 26rpx;
                        width: 144rpx;
                        height: 48rpx;
                        border-radius: 20rpx;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        box-sizing: border-box; // ‰ΩøÁî®border-boxÁõíÊ®°Âûã
                    }
                }
            }
        }
    }

    .loadMore {
        padding-bottom: calc(env(safe-area-inset-bottom) + 50);
    }

    // ÊöÇÊó†Êï∞ÊçÆÁä∂ÊÄÅ
    .empty-state {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        min-height: calc(100vh - 200rpx); // Á°Æ‰øùÂç†Êª°Ââ©‰ΩôÂ±èÂπïÈ´òÂ∫¶
        padding: 120rpx 60rpx;
        text-align: center;
        background-color: #ffffff;

        .empty-icon {
            font-size: 120rpx;
            margin-bottom: 30rpx;
            opacity: 0.3;
        }

        .empty-text {
            font-size: 32rpx;
            color: rgba(61, 61, 61, 0.6);
            margin-bottom: 16rpx;
            font-weight: 500;
        }

        .empty-desc {
            font-size: 26rpx;
            color: rgba(61, 61, 61, 0.4);
            line-height: 1.5;
        }
    }

    // Ëá™ÂÆö‰πâÂØºËà™Ê†èÂ≠ó‰ΩìÂ§ßÂ∞è‰∏∫34rpx
    :deep(.uni-navbar__content-title) {
        font-size: 34rpx !important;
    }

    :deep(.uni-nav-bar-text) {
        font-size: 34rpx !important;
    }
}
</style>
